import React, { useState, useEffect } from 'react';
import todo from '../images/todo.png';

const getLocalItems = () => {
	let list = localStorage.getItem("list");
  
	if (list) {
	  	return JSON.parse(list);
	} 
	else {
	 	return [];
	}
}

const ToDo = () => {
	const [inputData, setInputData] = useState('');
	const [items, setItems] = useState(getLocalItems());
	const [toggleSubmit, setToggleSubmit] = useState(true);
	const [isEditedItem, setIsEditedItem] = useState(null);

	const addItem = () => {
		if(!inputData){
			alert("Jaa be nalle");
		}
		else if(inputData && !toggleSubmit){
			setItems(
				items.map((elem) => {
					if(elem.id === isEditedItem){
						return {...elem, name: inputData}
					}
					return elem
				})
			)
			setToggleSubmit(true);
			setInputData("");
			setIsEditedItem(null);
		}
		else{
			const allInputData = {id: new Date().getTime().toString(), name: inputData}
			setItems([...items, allInputData]);
			setInputData("");
		}
		
	}

	const editItem = (id) => {
		let editedItem = items.find((elem) => {
			return elem.id === id;
		})
		setToggleSubmit(false);
		setInputData(editedItem.name);
		setIsEditedItem(id);
	}

	const deleteItem = (id) => {
		const updatedItems = items.filter((elem) => {
			return elem.id !== id;
		})
		setItems(updatedItems);
	}

	const removeAll = () => {
		setItems([]);
	}

	useEffect(() => {
		localStorage.setItem("list", JSON.stringify(items));
	}, [items]);
	
	return (
		<>
			<div className="main-div">
				<div className="child-div">
					<figure>
						<img src={todo} alt="todo list" />
						<figcaption>Add your list here</figcaption>
					</figure>
					<div className="add-items">
						<input type="text" placeholder="start making list..." value={inputData} onChange={(e) => setInputData(e.target.value) }/>
						{
							toggleSubmit ? <i className="fa fa-plus"  onClick={addItem} title="Add"></i> :
							<i className="fa fa-edit" onClick={addItem} title="Update"></i>
						}
					</div>
					<div className="show-items">
						{
							items.map((elem) => {
								return (
										<div className="each-item" key={elem.id}>
											<h3>{elem.name}</h3>
											<div className="todo-btn">
												<i className="far fa-edit" onClick={() => editItem(elem.id)} title="Edit"></i>
												<i className="far fa-trash-alt" onClick={() => deleteItem(elem.id)} title="Delete"></i>
											</div>         
										</div>									
								)
							})
						}
						
					</div>
					<div className="show-items">
						<button className="btn effect04" data-sm-link-text="Remove All" onClick={removeAll}>
							<span>Check List</span>
						</button>
					</div>
				</div>
			</div>
		</>
	)
}

export default ToDo
